.canvas {
  min-height: 100%;
  background-color: #fff;
  overflow: hidden;
}
.component-wrapper {
  margin: 12px;
  border: 1px solid #fff;
  padding: 12px;
  border-radius: 3px;
  &:hover {
    border-color: #d9d9d9;
  }
}
.selected {
  border-color: #1890ff !important;
}
.locked {
  opacity: 0.5;
  cursor: not-allowed;
}
.component {
  pointer-events: none; //disable the click event on the component
}
